@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

body {
  font-family: 'Oswald', sans-serif;
  overflow: hidden;
}

.nui-wrapper {
  position: absolute;
  left: 50%;

  transform: translate(-50%, -10%);
  margin: 0 auto;
  transition: all 1s;
  animation: SlideUp 0.5s;
  animation-fill-mode: forwards;
  overflow: hidden;

  display: flex;
  flex-direction: column;

  padding: 4px;

}

.nui-wrapper.false.blackbars-true {
  animation: SlideDown-Bars 0.5s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.nui-wrapper.true.blackbars-true {
  bottom: 120px;
  animation: SlideUp-Bars 0.5s;
}

.nui-wrapper.false.blackbars-false {
  animation: SlideDown 0.5s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.progress-bar-text {
  text-align: center;

  font-family: Oswald;
  font-style: normal;
  font-weight: 500;
  font-size: 28px;
  line-height: 41px;

  color: #FFFFFF;

  text-shadow: 0px 0px 4px #000000, 0px 0px 2px #000000;

}

.progress-bar-desc {

  margin-top: 1px;

  text-align: center;

  font-family: Oswald;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  /* identical to box height */
  
  
  color: #FFFFFF;
  
  text-shadow: 0px 0px 4px #000000, 0px 0px 2px #000000;
}

.progress-bar {

  width: 360px;
  height: 40px;

  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 57, 98, 0.4) 0%, rgba(36, 39, 63, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  border-radius: 3px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

  align-items: center;
  transition: all 1s;

  margin-top: 8px;

  padding-left: 16px;
  padding-right: 16px;

}

.progress-inside {

  position: relative;

  height: 8px;

  background: linear-gradient(90deg, #4E65A7 -1.57%, #56FDFD 102.99%);
  border-radius: 4px 0px 0px 4px;

  bottom: 9px;

  transition: 0.1s ease;
}

.progress-inside-shadow {

  position: relative;

  height: 8px;
  transition: all 0.05s;

  margin-bottom: 16px;

  background: rgba(255, 255, 255, 0.16);  
  border-radius: 4px;

  top: 15px;

}

.progress-bar > div {
  flex: 1;
}

.progres-text > h1 {
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  letter-spacing: 0.205em;
  text-transform: uppercase;
  z-index: 1000;
  margin-left: 10px;
}

.progres-text > h2 {
  z-index: 1000;

  font-family: Nunito Sans;
  font-style: normal;
  font-weight: 300;
  font-size: 10px;

  display: flex;
  align-items: center;
  text-align: right;
  letter-spacing: 0.205em;

  color: #FFFFFF;
  margin-left: auto;

  margin-right: 10px;
}

.progress-bar-cancel {

  display: flex;
  flex-direction: row;

  width: max-content;

  margin-top: 8px;
  text-align: center;

  padding: 16px;

  background: radial-gradient(50% 50% at 50% 50%, rgba(43, 57, 98, 0.4) 0%, rgba(36, 39, 63, 0.7) 100%);
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  border-radius: 3px;

  font-family: Oswald;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;

  color: white;

  align-items: center;
  justify-content: center;

  align-self: center;

}

.rmb {
  width: 18px;
  height: 24px;

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)) drop-shadow(0px 0px 2px #000000);

}

.tocancel {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;

  text-shadow: 0px 0px 8px #000000, 0px 0px 2px #000000;
}

@keyframes SlideDown {
  0% { bottom: 30px; }
  100% { bottom: -200px; }
}

@keyframes SlideUp {
  0% { bottom: -200px; }
  100% { bottom: 30px; }
}

@keyframes SlideDown-Bars {
  0% { bottom: 120px;  }
  100% { bottom: -200px; }
}

@keyframes SlideUp-Bars {
  0% { bottom: -200px; }
  100% { bottom: 120px; }
}